/**
  Tutor Notice
   */
.tnotice {
  text-align: left;
  padding: 10px 0;
  background-color: #fff;
  border-radius: 4px;
  position: relative;
  margin-bottom: 10px;
}

.tnotice:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.tnotice__icon {
  position: absolute;
  top: 50%;
  left: 22px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
  padding: 7px;
  border-radius: 50%;
  display: inline-block;
  color: #fff;
  text-align: center;
  line-height: 11px;
}

.tnotice__type {
  color: #3e3e3e;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0;
}

.tnotice__message {
  font-size: 14px;
  margin-top: 0;
  margin-bottom: 0;
  color: #878787;
}

.tnotice__content {
  padding-left: 70px;
  padding-right: 60px;
}

.tnotice__close {
  position: absolute;
  right: 22px;
  top: 50%;
  width: 14px;
  cursor: pointer;
  height: 14px;
  fill: #878787;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.tnotice--success .tnotice__icon {
  background-color: #2bde3f;
}

.tnotice--success:before {
  background-color: #2bde3f;
}

.tnotice--blue .tnotice__icon {
  background-color: #1d72f3;
}

.tnotice--blue:before {
  background-color: #1d72f3;
}

.tnotice--danger .tnotice__icon {
  background-color: #f31e1c;
}

.tnotice--danger:before {
  background-color: #f31e1c;
}

