  .report-course-list-wrap .tutor-list-table [class$="-link"] {
	  color: #b9bac3;
  }
  
  .report-course-list-wrap .tutor-list-table a:hover {
	  color: #3057d5;
  }
  
  .report-course-list-wrap .tutor-list-table .course-link {
	  font-size: 12px;
	  margin-left: 5px;
  }
  
  .tutor-list-table .course-percentage {
