	  height: 4px;
	  border-radius: 2px;
	  background-color: #dcdce1;
	  position: relative;
  }
  
  .tutor-list-table .course-percentage::before {
	  content: "";
	  position: absolute;
	  width: var(--percent);
	  height: 4px;
	  border-radius: 2px;
	  background-color: #3e64de;
  }
  
  .report-course-list-wrap
