	  transition: 0.2s ease;
  }
  
  .tutor-report-content a:hover,
  .tutor-report-content a:focus,
  .report-student-profile .profile-table a:hover {
	  color: #3057d5;
  }
  
  @media (max-width: 991px) {
	  .report-course-list-wrap .tutor-list-table th:nth-child(n + 7),
